.products h2{
    margin-bottom: 20px;
}

.products ul{
    list-style: square;
    padding: 30px;
}

.products li{
    margin-bottom: 20px;
}

.products a{
    text-align: none;
    color: #333;
    -webkit-text-decoration: none;
    text-decoration: none;
}

.products a:hover{
    -webkit-text-decoration: underline;
    text-decoration: underline;
    color: black;
}